<template>
  <div class="login-container">
    <!-- 登录 -->
    <div class="login-wrap">
      <div class="login">
        <div class="loginform">
          <ul class="tab clearFix">
            <li>
              <a href="javaScript:void(0)" style="border-right: 0;" @click=QR_login() :class="rea_className.a_QR">扫描登录</a>
            </li>
            <li>
              <a href="javaScript:void(0);" :class="rea_className.a_account" @click="account_login()">账户登录</a>
            </li>
          </ul>

          <div class="account current" ref="node_account">
            <form action="##" @click="($event) => {$event.preventDefault()
              
            }">
              <div class="input-text clearFix">
                <span></span>
                <input type="text" placeholder="邮箱/用户名/手机号" v-model="userName">
                <a href="javaScript:void(0);" :class="rea_dis_attention.userName">账户名必须有8-16位字母数字或是下划线组成</a>
              </div>
              <div class="input-text clearFix">
                <span class="pwd"></span>
                <input type="password" ref="node_input_password" placeholder="请输入密码" v-model="password" required>
                <a href="javaScript:void(0);" :class="rea_dis_attention.password" >密码必须有8-16位字母数字或是下划线组成</a>
                <span class="unvisible" @click="fn_visible_password($event)"></span>
              </div>
              <div class="setting clearFix">
                <label class="checkbox inline">
                  <input name="m1" type="checkbox" value="2" checked="">
                  自动登录
                </label>
                <span class="forget">忘记密码？</span>
              </div>
              <button class="btn" @click="login_submit($event)" >登&nbsp;&nbsp;录</button>
            </form>

            <div class="call clearFix">
              <ul>
                <li><img src="./images/qq.png" alt=""></li>
                <li><img src="./images/sina.png" alt=""></li>
                <li><img src="./images/ali.png" alt=""></li>
                <li><img src="./images/weixin.png" alt=""></li>
              </ul>
              <router-link class="register" to="/register">立即注册</router-link>
            </div>
          </div>
          <div class="QR " ref="node_QR">
            <div class="QR_wrap">
              <div class="QR_zhifubao_wrap current" ref="node_zhifubao_wrap">
                <span>支付宝扫一扫</span>
                <label for="refresh">
                  <i class="iconfont icon-shuaxin" ref="node_refresh_icon"></i>
                  <em>刷新一下</em>
                </label>
                <button id="refresh" @click="peace($event)"></button>
                <img src="./images/zhifubao.jpg"  alt="">
              </div>
              <div class="QR_weixin_wrap " ref="node_weixin_wrap">
                <span>微信扫一扫</span>
                <label for="refresh2">
                  <i class="iconfont icon-shuaxin" ref="node_refresh_icon"></i>
                  <em>刷新一下</em>
                </label>
                <button id="refresh2" @click="peace($event)"></button>
                <img src="./images/weixin.jpg"  alt="">
              </div>
            </div>
            <div class="choose_channel">
              <span>选择登陆方式：</span>
              <div class="choose">
                <img src="./images/ali.png" alt="zhifubao" @click="choose_channel($event)">
                <img src="./images/weixin.png" alt="weixin" @click="choose_channel($event)">
              </div>

             </div>

          </div>

             


        </div>
      </div>
    </div>
    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
      </ul>
      <div class="address">M78星云</div>
      <div class="beian">二番队插旗手
      </div>
    </div>
  </div>
</template>

<script>

import router from '@/router'
import store from '@/store'
import { computed } from '@vue/reactivity'

import {ref,reactive,watch} from 'vue'

  export default {
    name: 'Login',
    setup(){
      let rea_className = reactive({'a_QR':'','a_account':'current'})
      let status = reactive({'submit':{'ability':{'useName':false,'password':false}}})
      let rea_dis_attention = reactive({'userName':'attention','password':'attention'})
      let userName = ref('')
      let password = ref('')
      let node_account = ref()
      let node_QR = ref()
      let node_refresh_icon= ref()
      let node_zhifubao_wrap = ref()
      let node_weixin_wrap =ref()
      let node_input_password = ref()

      console.log(node_QR,node_QR.value,'setup node ');

      watch(userName,() => {
        console.log('1111');
        let reg= /^[a-zA-Z0-9]\w{5,16}$/
        let judge = reg.test(userName.value)
        console.log(judge);

        status.submit.ability.userName = judge
        if(!judge){
          rea_dis_attention.userName = 'attention unable'

        }else{rea_dis_attention.userName = 'attention'}

      })
      watch(password,() => {
        let reg= /^[a-zA-Z0-9]\w{5,16}$/
        let judge = reg.test(password.value)

        status.submit.ability.password = judge
        if(!judge){
          rea_dis_attention.password = 'attention unable'

        }else{rea_dis_attention.password = 'attention'}
      })

      //点击扫码登陆
      function QR_login($event){

        rea_className.a_account = ''
        rea_className.a_QR = 'current'
        node_account.value.style.display=('none')
        node_QR.value.style.display=('block')
        console.log(rea_className,'rea_className');

      }
      //账户登陆
      function account_login($event){
        rea_className.a_QR = ''
        rea_className.a_account = 'current'
        node_QR.value.style.display=('none')
        node_account.value.style.display=('block')

      }

      function refresh_button($event){

       
        let e = $event||window.event
        e.target.previousElementSibling.firstElementChild.classList.add('active')
        e.target.nextElementSibling.classList.add('fadein')

        let button_timer = setTimeout(() => {
          e.target.previousElementSibling.firstElementChild.classList.remove('active')
          e.target.nextElementSibling.classList.remove('fadein')
          clearTimeout(button_timer)
         },1000)


        
      }
      //节流函数
      function throttle(){
        let timer = null
        return function refresh(){
          console.log(timer,'again timer');
          if(!timer){
            timer =  setTimeout(refresh_button,0)

          }else{
            timer = clearTimeout(timer)
            console.log(timer,'after timer');
            timer =  setTimeout(refresh_button,0)

          }
        }

      }
      let triggle =  throttle()

      //防抖函数
      function debounce(){
        let flag = true
        let timer = null
        return function refresh($event){

         

          if(flag){
            flag = false
            refresh_button($event)
            timer = setTimeout(() => {
               flag = true;
               timer = clearTimeout(timer)
             },1000)

          }
        }
      }
      let peace = debounce()

       //改变渠道方式
      function choose_channel(e){
        let alt = e.target.alt
        if(alt =='zhifubao'){
          node_weixin_wrap.value.classList.remove('current')
          node_zhifubao_wrap.value.classList.add('current')
        }else if(alt=='weixin'){
          node_zhifubao_wrap.value.classList.remove('current')
          node_weixin_wrap.value.classList.add('current')
        }
      }

      function login_submit($event){
        let e = $event||window.event
        e.preventDefault()
        e.stopPropagation()
        if(status.submit.ability.userName&&status.submit.ability.password){
          $event.target.innerText = '登陆中---'
          //登陆验证成功后在store中得知，然后在watch监视中跳转路由并保存token和cookie等关键信息
          store.dispatch('loginSubmit',{'userName':userName,'password':password})

        }else{

        }


 
        console.log(userName.value,password.value);

        // store.dispatch('reqLogin',{'userName':userName.value,'password':password.value,'auto':true})//auto为确认是自动登陆的标记
        
      }
      function fn_visible_password(e){
        e = e ||window.event
        e.target.className = e.target.className =='visible'?'unvisible':'visible'
        node_input_password.value.type = e.target.className =='visible'?'text':'password'
      }

      let message = computed(() => {
        return store.state.login.message
        
      })
      watch(message,() => {
        if(message.state==1){
          
          router.go(-1)
        };
      })
      return {
        QR_login,
        account_login,
        refresh_button,
        triggle,
        peace,
        choose_channel,
        fn_visible_password,
        rea_className,
        rea_dis_attention,
        node_QR,
        node_account,
        node_refresh_icon,
        node_zhifubao_wrap,
        node_weixin_wrap,
        node_input_password,
        userName,
        password,
        status,
        login_submit,
        message
      }
    }
  }
</script>

<style lang="scss" scoped>

  .login-container {
    .login-wrap {
      height: 487px;
      background-color: #e93854;

      .login {
        width: 1200px;
        height: 487px;
        margin: 0 auto;
        background: url(./images/loginbg.png) no-repeat;
      }

      .loginform {
        width: 420px;
        height: 406px;
        box-sizing: border-box;
        background: #fff;
        float: right;
        top: 45px;
        position: relative;
        padding: 20px;

        .tab {

          li {
            width: 50%;
            float: left;
            text-align: center;

            a {
              width: 100%;
              display: block;
              height: 50px;
              line-height: 50px;
              font-size: 20px;
              font-weight: 700;
              color: #333;
              border: 1px solid #ddd;
              box-sizing: border-box;
              text-decoration: none;

            }

            .current {
              border-bottom: none;
              border-top-color: #28a3ef;
              color: #e1251b;
            }
          }
        }
        
        .account{
          width: 380px;
          height: 316px;
          box-sizing: border-box;
          border: 1px solid #ddd;
          border-top: none;
          padding: 18px;
          display: none;


          form {
            margin: 15px 0 18px 0;
            font-size: 12px;
            line-height: 18px;

            .input-text {
              margin-bottom: 16px;

              span {
                float: left;
                width: 37px;
                height: 32px;
                border: 1px solid #ccc;
                background: url(../../assets/images/icons.png) no-repeat -10px -201px;
                box-sizing: border-box;
                border-radius: 2px 0 0 2px;
              }

              .pwd {
                background-position: -72px -201px;
              }

              input {
                width: 302px;
                height: 32px;
                box-sizing: border-box;
                border: 1px solid #ccc;
                border-left: none;
                float: left;
                padding-top: 6px;
                padding-bottom: 6px;
                font-size: 14px;
                line-height: 22px;
                padding-right: 8px;
                padding-left: 8px;
                border-radius: 0 2px 2px 0;
                outline: none;
              }
              .attention{
                display: none;
                &.unable{
                  display: inline;
                  color: #e93854;
                }
              }
              .visible{
                width: 20px;
                height: 20x;
                background-image: url(./images/visible.jpg);
                background-size: contain;
                background-position: 0px ;
                position: absolute;
                right: 45px;
              }
              .unvisible{
                @extend .visible;
                background-image:url(./images/unvisible.jpg) ;
              }
            }

            .setting {
              label {
                float: left;
              }

              .forget {
                float: right;
              }
            }

            .btn {
              background-color: #e1251b;
              padding: 6px;
              border-radius: 0;
              font-size: 16px;
              font-family: 微软雅黑;
              word-spacing: 4px;
              border: 1px solid #e1251b;
              color: #fff;
              width: 100%;
              height: 36px;
              margin-top: 25px;
              outline: none;
            &:hover{
              cursor: pointer;
              }
            }

          }

          .call {
            margin-top: 30px;

            ul {
              float: left;

              li {
                float: left;
                margin-right: 5px;
              }
            }

            .register {
              float: right;
              font-size: 15px;
              line-height: 38px;
            }

            .register:hover {
              color: #4cb9fc;
              text-decoration: underline;
            }
          }
          &.current{
            display: block
          }
        }

        .QR{
          @extend .account;
    
          .QR_wrap{
            div{
              display: none;
              span{
                float: left;
                overflow: hidden;
              }
              label{
                float: right;
                width: 100px;
                height: 20px;
                font-size: 10px;
                overflow: hidden;
                cursor: pointer;
                i{  
                  display: block;
                  float: left;
                  &.active{
                    transform: rotate(2turn);
                    transition: all 1s;
                  }
                   
                }
                em{
                  float: right;
                  width: 70px;
                  font-size: 16px;
                  background-color: #e1251b;
                }
              }
              button{
                display: none;
              }
              
              &.current{
                display: block;
              }
              @keyframes fadein {
                from{
                  opacity: 0;
                }
                to{
                  opacity: 1;
                } 
              }
              @-webkit-keyframes fadein{
                from{
                  opacity: 0;
                }
                to{
                  opacity: 1;
                } 

              }
              img{
                width: 200px;
                height: 200px;
                margin-left:65px;
                display: block;
                &.fadein{
                  animation-name: fadein;
                  animation-duration: 3s;

                }
      
              }

            }


              
            }
          
          .choose_channel{
            margin-top: 10px;
            text-align: center;
            span{
              float: left;
              line-height: 40px;
              font-size: 14px;
            }
        
            .choose{
              float: left;
              overflow: hidden;
              img{
                width: 30px;
                display: inline-block;
             
              }

            }
            &::after{
              content: '';
              clear: both;
              display: block;
              overflow: hidden;
            }
          }
 

        }
      }
    }

    .copyright {
      width: 1200px;
      margin: 0 auto;
      text-align: center;
      line-height: 24px;

      ul {
        li {
          display: inline-block;
          border-right: 1px solid #e4e4e4;
          padding: 0 20px;
          margin: 15px 0;
        }
      }
    }

  }
</style>